


.posts{
	grid-area: posts;
	display: flex;
	flex-direction: column;
	gap: 30px;
	padding-top: 30px;
	justify-self: start;
	border-top: 1px solid rgb(237, 241, 245);
	border-left: 1px solid rgb(237, 241, 245);
	border-right: 1px solid rgb(237, 241, 245);
}

.postCreate{
	&__content {
		padding: 0 20px 20px 20px;
		border-bottom: 1px solid rgb(237, 241, 245);
	}
	&__body{
		
		display: flex;
		align-items: center;
		gap: 10px;
		textarea{
			width: 100%;
			
			// min-width: 450px;
			border: 1px solid rgb(129, 29, 222);
			padding: 10px;
			border-radius: 8px;
		}
		button {
			display: flex;
			justify-self: center;
			align-items: center;
			background: linear-gradient(to right, rgb(189, 139, 237), rgb(129, 29, 222));
			box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
			color: #fff;
			padding: 2px 28px;
			height: 40px;
			border-radius: 18px;
			transition: all 0.7s ease 0s;
			&:hover{
				opacity: 0.8;
			}
		}
	}

	&__icons {
		margin-top: 8px;
		display: flex;
		gap: 10px;
		align-items: center;
		justify-content: flex-start;
		
	}

	&__icon {
		cursor: pointer;
		svg {
				width: 20px;
				height: 20px;
			}
			&:hover{
				opacity: 0.8;
			}
	}
	.filter{

	}
}